export default {
    name: "WeatherA",
    data() {
        return {
            temp: null,
            city: null
        }
    },
    methods: {
        getWeather() {
            let A = {
                "temperature": this.temp,
                "city": this.city
            }
            let Action = {
                "content": JSON.stringify(A),
                "id": "63555cb456586d005905995f"
            }
            let nameAct = {
                "name": "Weather",
                "description": "Get the weather of a city"
            }
            localStorage.action = JSON.stringify(Action);
            localStorage.nameact = JSON.stringify(nameAct);
            this.$router.push({ name: 'Recap' });
        },
    }
}
</script>

